const accordion = document.querySelector('.accordion');
const accordionPanels = document.querySelectorAll('.accordion__panel');
const accordionTriggers = document.querySelectorAll('.accordion__btn');
let speedAnimation = 300;

accordionTriggers.forEach((trigger) => {
	trigger.addEventListener('click', (e) => {
		let panel = trigger.parentNode.parentNode.querySelector(
			'.accordion__content'
		);
		if (e.target.parentNode.parentNode.classList.contains('is-active-ac')) {
			slideUp(panel, speedAnimation);

			panel.addEventListener(
				'transitionrun',
				() => {
					trigger.parentNode.parentNode.classList.remove('is-active-ac');
				},
				{ once: true }
			);
		} else {
			accordionPanels.forEach(function (item) {
				if (item.classList.contains('is-active-ac')) {
					slideUp(item.querySelector('.accordion__content'), speedAnimation);

					item.querySelector('.accordion__content').addEventListener(
						'transitionrun',
						() => {
							item.classList.remove('is-active-ac');
						},
						{ once: true }
					);
				}
			});
			trigger.parentNode.parentNode.classList.add('is-active-ac');
			slideDown(panel, speedAnimation);
		}
	});
});

/* Side Animation */
let slideUp = (target, duration = 500) => {
	target.style.transitionProperty = 'height, margin, padding';
	target.style.transitionDuration = duration + 'ms';
	target.style.boxSizing = 'border-box';
	target.style.height = target.offsetHeight + 'px';
	target.offsetHeight;
	target.style.overflow = 'hidden';
	target.style.height = 0;
	target.style.paddingTop = 0;
	target.style.paddingBottom = 0;
	target.style.marginTop = 0;
	target.style.marginBottom = 0;
	window.setTimeout(() => {
		target.style.display = 'none';
		target.style.removeProperty('height');
		target.style.removeProperty('padding-top');
		target.style.removeProperty('padding-bottom');
		target.style.removeProperty('margin-top');
		target.style.removeProperty('margin-bottom');
		target.style.removeProperty('overflow');
		target.style.removeProperty('transition-duration');
		target.style.removeProperty('transition-property');
		//alert('!');
	}, duration);
};

let slideDown = (target, duration = 500) => {
	target.style.removeProperty('display');
	let display = window.getComputedStyle(target).display;

	if (display === 'none') display = 'block';

	target.style.display = display;
	let height = target.offsetHeight;
	target.style.overflow = 'hidden';
	target.style.height = 0;
	target.style.paddingTop = 0;
	target.style.paddingBottom = 0;
	target.style.marginTop = 0;
	target.style.marginBottom = 0;
	target.offsetHeight;
	target.style.boxSizing = 'border-box';
	target.style.transitionProperty = 'height, margin, padding';
	target.style.transitionDuration = duration + 'ms';
	target.style.height = height + 'px';
	target.style.removeProperty('padding-top');
	target.style.removeProperty('padding-bottom');
	target.style.removeProperty('margin-top');
	target.style.removeProperty('margin-bottom');
	window.setTimeout(() => {
		target.style.removeProperty('height');
		target.style.removeProperty('overflow');
		target.style.removeProperty('transition-duration');
		target.style.removeProperty('transition-property');
	}, duration);
};

let slideToggle = (target, duration = 500) => {
	if (window.getComputedStyle(target).display === 'none') {
		return slideDown(target, duration);
	} else {
		return slideUp(target, duration);
	}
};
